import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { AuthService } from '../../angular-services/auth.service';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.css']
})
export class LoginFormComponent implements OnInit {

  formLogin: FormGroup;
  message: string = '';

  constructor(private authService: AuthService, private router: Router) { }

  ngOnInit() {
    this.formLogin = new FormGroup({
      user: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required)
    });
  }

  login() {
    if (this.formLogin.valid) {
      let data = this.formLogin.value;
      this.authService.login(data.user, data.password).subscribe(
        response => {
          if (response !== undefined) {
            if (response.auth) {
              localStorage.setItem('tokenUserLogged', response.token);
              this.router.navigate(['/admin/home']);
            } else {
              if (response.code === 2) {
                this.message = 'Nenhum usuário cadastrado com esses dados!';
              } else if (response.code === 3) {
                this.message = 'Senha inválida!';
              }
            }
          } else {
            this.message = 'Houve algum erro! Contacte o administrador!';
          }
        }
      )
    }
  }

}
